// Import CWD Cornell Framework variables
@import "cwd-variables";

/* Cornell University Center for Teaching Innovation
   ************************************ */

$project-font-family-sans-serif: Open Sans, -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica Neue, sans-serif;
$project-font-family-serif-alt: $cwd-font-family-serif-alt;
$project-font-family-sans-alt: Lato, Assistant, -apple-system, system-ui, BlinkMacSystemFont, 'Segoe UI', Roboto, Helvetica Neue, sans-serif;

// Color
$project-color-link: #b31b1b; // default text link color
$project-color-link-hover: #ed0000; // text link :hover color (also used for :focus)
$project-color-link-events-hover: #1177ae; //contrast compliant link hover for light gray backgrounds
$project-color-link-hover-ow: #e10000; //contrast compliant link color for offwhite background elements with red
$project-color-link-active: #000; // text link :active color
$project-color-accent-heading-h2: #16486a; // Applies an accent color to basic heading level 1, informed by your custom design (use #000 to pass on this option)
$project-color-accent-heading-h3: #497792; // Applies an accent color to basic heading level 2, informed by your custom design (use #000 to pass on this option)
$project-color-accent-heading-light: #3787b0; // Applies an accent color to basic heading level 3, informed by your custom design (use #000 to pass on this option)
$project-color-min-contrast: #717171; // The minimum darkness of gray text over off-white background to meet accessibility guidelines


/* Typography */
main .primary, main .secondary, body footer, body > .band[role='complementary'] {
	font-family: $project-font-family-sans-serif;
	font-size: 15px;
	font-weight: 400;
}
.sans {
	font-family: $project-font-family-sans-serif;
	font-weight: 400;
}
main .secondary, body > footer, .smallprint, .table {
	font-size: 14px;
}
h1, .h1, #cu-header .cu-brand h2 {
	font-weight: 400;
	font-family: $project-font-family-serif-alt;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
h2, h3, h4, h5, h6, .h2, .h3, .h4, .h5, .h6 {
	font-family: $project-font-family-sans-alt;
	font-weight: 300;
}
h2, .h2 {
	font-size: 29px;
}
h3, .h3 {
	font-size: 21px;
	font-weight: 400;
}
h4, h5, h6, .h4, .h5, .h6 {
	font-weight: 400;
}
main h1, #main h1, main .h1, #main .h1 {
	color: #333;
}
main h2, #main h2, main .h2, #main .h2, .tutorial {
	color: $project-color-accent-heading-h2;
}
main h3, #main h3, main .h3, #main .h3, strong.tutorial {
	color: $project-color-accent-heading-h3;
}
main p {
	line-height: 1.7;
}
main .intro {
	font-family: $project-font-family-sans-serif;
	font-size: 20px;
	font-weight: 300;
	line-height: 1.5;
	margin-top: -5px;
}
code {
	color: #444;
}
a {
	color: $project-color-link;
}
a:hover, a:focus {
	color: $project-color-link-hover;
}
a:active {
	color: $project-color-link-active;
}
main p + h2, main ul + h2, main ol + h2 {
	margin-top: 1.25em;
}
#main-article hr {
	clear: both;
	float: left;
	width: 100% !important;
	margin: 1.5em 0 2.25em;
}

/* Icon Fonts */
.contact-us:before {
	font: normal normal normal 14px/1 FontAwesome;
	speak: none;
	font-size: inherit;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.secondary-navigation ul ul a:after, .metadata-set .deco:before {
	display: inline-block;
	font: normal normal normal 14px/1 'Material-Design-Iconic-Font';
	font-size: inherit;
	speak: none;
	text-rendering: auto;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}


/* Page Header */
.cu-seal #cu-header {
	background: -moz-linear-gradient(-10deg, #ffffff 0%, #ededed 100%);
	background: -webkit-linear-gradient(-10deg, #ffffff 0%,#ededed 100%);
	background: linear-gradient(170deg, #ffffff 0%,#ededed 100%);
}
#site-header {
	border-bottom: 2px solid #ddd;
}
.home #site-header {
	border-bottom: 0;
}
.cu-unit a {
	border-bottom: 2px solid transparent;
	transition: color 0.2s, border-color 0.2s;
}
.cu-unit a:hover, .cu-unit a:focus {
	color: #b31b2b;
	border-color: #e0bebe;
	text-decoration: none;
}
.cu-unit a:active {
	color: #000;
	border-color: #aaa;
	text-decoration: none;
}


/* Main Navigation */
// --> desktop navigation starts at MEDIUM grid

/* Section Navigation */
.secondary-navigation {
	margin: -10px 0 -40px;
}
.secondary-navigation + div {
	margin-top: 40px;
}
.secondary-navigation ul li {
	font-family: $project-font-family-serif-alt;
	font-weight: 400;
	line-height: 1.3;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.secondary-navigation ul li > a, .secondary-navigation > .mobile > ul > li.menu-item--active-trail > a {
	color: $project-color-link;
}
.secondary-navigation ul ul li, .secondary-navigation ul ul li > a {
	font-family: $project-font-family-sans-alt;
	padding: 13px 0;
	font-weight: 400;
	-webkit-font-smoothing: auto;
	-moz-osx-font-smoothing: auto;
	border: 0;
}
.secondary-navigation ul ul ul {
	border-left-color: $project-color-accent-heading-light;
}
.secondary-navigation ul ul ul ul {
	border-left-color: #ddd;
}
.secondary-navigation .current-menu-item > a, .secondary-navigation .active > a, .secondary-navigation li > a.is-active {
	color: $project-color-accent-heading-light;
}
.secondary-navigation a:hover, .secondary-navigation a:focus,
.secondary-navigation ul ul li a:hover, .secondary-navigation ul ul li a:focus {
	color: $project-color-link;
}
.secondary-navigation a:active,
.secondary-navigation ul ul li a:active {
	color: #000;
}


/* Sidebar Spotlight */
.secondary figure {
	-moz-box-shadow: none;
	-webkit-box-shadow: none;
	box-shadow: none;
	padding-bottom: 5px;
	border-bottom: 3px solid #b31b1b;
}
.secondary .spotlight figcaption {
	font-family: $project-font-family-sans-serif;
	font-size: 14px;
}

/* Sidebar Contact */
.contact-us {
	display: flex;
	align-items: center;
	text-decoration: none;
	background: #497792;
	color: #fff;
	padding: 8px 16px 8px 16px;
	font-size: 24px;
	font-weight: 300;
	transition: background 0.2s, color 0.1s, border-color 0.2s;
	max-width: 320px;
	margin: 0 0 40px;
}
.contact-us:before {
	content: '\f086';
	font-size: 48px;
	margin-right: 20px;
	padding-right: 20px;
	border-right: 2px solid rgba(255,255,255,0.3);
	opacity: 0.75;
	transition: opacity 0.2s;
	padding-left: 2px;
}
.contact-us:hover:before, .contact-us:focus:before {
	opacity: 1;
}
.contact-us:hover, .contact-us:focus {
	background: #5390b4;
	color: #fff;
}
.contact-us:active {
	background: #375d73;
	color: #fff;
}
.contact-us.light {
	background: none;
	border: 3px solid #c7d5dd;
	color: #497792;
	white-space: nowrap;
	margin-left: auto;
	margin-right: auto;
}
.contact-us.light:before {
	color: rgba(179,27,27,0.6) !important;
}
.contact-us.light:hover, .contact-us.light:focus {
	border-color: #8db2c8;
	color: #1b4055;
}
.contact-us.light:hover:before, .contact-us.light:before {
	color: #497792;
}
.contact-us.light:active {
	background: rgba(0,0,0,0.1);
}
.contact-us.light:before {
	border-color: rgba(179,27,27,0.4);
}


/* Supplementary Band */
body > .band.accent1 {
	background: #497792;
	color: #fff;
	padding: 40px 0;
	overflow: hidden;
}
body > .band.accent1 .cwd-component .date, body > .band.accent1 .cwd-component .date .icon {
	color: #fff;
}
body > .band.accent1 a {
	color: #fff;
	-moz-text-decoration-color: rgba(255,255,255,0.5);
	-webkit-text-decoration-color: rgba(255,255,255,0.5);
	text-decoration-color: rgba(255,255,255,0.5);
}
body > .band.accent1 a:hover, body > .band.accent1 a:focus {
	-moz-text-decoration-color: #fff;
	-webkit-text-decoration-color: #fff;
	text-decoration-color: #fff;
}
body > .band.accent2 {
	background: #f9f9f9;
	padding: 40px 0;
}
body > .band.accent2 .h1 {
	color: $project-color-accent-heading-h2;
}
body > .band.accent2 .card .group-image {
	border-bottom: 5px solid #b31b1b;
}
body > .band.accent2 a {
	-moz-text-decoration-color: rgba(179,27,27,0.6);
	-webkit-text-decoration-color: rgba(179,27,27,0.6);
	text-decoration-color: rgba(179,27,27,0.6);
	font-size: 16px;
}
.footer-toolkit {
	margin: 0;
	text-align: center;
}
.secondary .footer-toolkit {
	margin: 1.5em 0;
	float: left;
	width: 100%;
}
.footer-toolkit .h1 {
	font-size: 22px;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.footer-toolkit .flex {
	align-items: stretch;
	flex-wrap: wrap;
}
.footer-toolkit .flex > * {
	display: flex;
	align-items: stretch;
	width: 100%;
	padding: 0;
	margin: 10px 0;
}
.footer-toolkit .flex a {
	background: #eaeef0;
	color: #004466 !important;
	text-decoration: none;
	display: block;
	width: 100%;
	text-align: center;
	padding: 20px 15px;
	border-radius: 4px;
	text-transform: uppercase;
	font-size: 14px;
	line-height: 1.4;
	transition: background 0.2s, color 0.2s;
}
.footer-toolkit .flex a:hover, .footer-toolkit .flex a:focus {
	background: #fff;
	color: #000 !important;
}
.secondary .footer-toolkit .flex a:hover, .secondary .footer-toolkit .flex a:focus {
	background: rgba(0,0,0,0.04);
}
.footer-toolkit .flex a:active {
	background: #bbb;
	transition: none;
}
.footer-toolkit .flex .view-all a {
	background: none;
	border: 3px solid #fff;
}
.accent1 .footer-toolkit .flex .view-all a {
	color: #fff !important;
}
.footer-toolkit .flex .view-all a:hover, .footer-toolkit .view-all a:focus {
	background: rgba(0,0,0,0.15);
	color: #fff !important;
}
.footer-toolkit .flex .view-all a:active {
	background: rgba(0,0,0,0.25);
}
.footer-toolkit .fa, .footer-toolkit .zmdi {
	font-size: 72px;
	margin-bottom: 3px;
}


/* Footer */
body > footer {
	background: #315163;
	color: #c3cdd2;
}
body, body > footer .sub-footer {
	background: #2e3337;
}
body > footer .main-footer .container-fluid:after {
	border-right: 2px solid #56707f;
}
body > footer h2 {
	color: #fff;
}
body > footer .list-menu.vertical li {
	border-color: #56707f;
}
.footer-links .zmdi, .footer-links .fa {
	color: #fff;
	font-size: 14px;
	position: relative;
	top: -1px;
	margin-right: 6px;
}
body > footer .footer-links a {
	color: #9bd6ff;
}
body > footer .footer-links a:hover, body > footer .footer-links a:focus {
	color: #fff;
}
body > footer .footer-links a:active {
	color: #333;
}
body > footer .main-footer .primary h2 {
	font-family: $cwd-font-family-serif-alt;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
body > footer .block {
	margin: 0;
}

/* Landing Page */
.landing-page .banner-photo {
	background: #281e16 url('../images/cti/sample_landing_page_banner.jpg') 50% -30px no-repeat;
	background-size: cover;
	min-height: 120px;
	max-height: 240px;
	height: 20vw;
	color: #fff;
	position: relative;
}
.landing-page .banner-photo:before {
	content: '';
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;

	background: -moz-linear-gradient(top, rgba(50,70,77,0) 50%, rgba(32,32,32,0.8) 100%);
	background: -webkit-linear-gradient(top, rgba(50,70,77,0) 50%,rgba(32,32,32,0.8) 100%);
	background: linear-gradient(to bottom, rgba(50,70,77,0) 50%,rgba(32,32,32,0.8) 100%);

	background: linear-gradient(to bottom, rgba(50,70,77,0) 50%,rgba(32,32,32,0.8) 100%), linear-gradient(135deg, rgba(36,72,84,0.3) 0%,rgba(50,70,77,0) 50%,rgba(32,32,32,0.5) 100%);
}
.landing-page .banner-photo .row {
	position: absolute;
	bottom: 22%;
	width: 100%;
}
.landing-page .banner-photo h1 {
	font-family: $project-font-family-sans-serif;
	font-weight: 500;
	font-size: 20px;
	width: 100%;
	text-align: center;
}
.landing-page #main-article > section > h1 {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	overflow: hidden;
	clip: rect(0,0,0,0);
	border: 0;
}


/* Homepage */
.banner-flex {
	background: #dbecf1;
	background: -moz-linear-gradient(-45deg, #f4f6f8 50%, #dbecf1 100%);
	background: -webkit-linear-gradient(-45deg, #f4f6f8 50%,#dbecf1 100%);
	background: linear-gradient(135deg, #f4f6f8 50%,#dbecf1 100%);
	position: relative;
}
#home-header .contextual-region {
	position: static;
}
#home-header {
	background: none !important;
	font-family: $project-font-family-sans-serif;
	-webkit-font-smoothing: subpixel-antialiased;
}
#home-header .secondary {
	text-align: center;
}
.quote {
	margin: 1em 0 2.5em;
	padding: 15px;
	border: double rgba(0,0,0,0.12);
}
.quote q {
	font-family: $project-font-family-serif-alt;
	font-size: 22px;
	font-style: italic;
	color: #333;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
.quote cite {
	font-style: normal;
}
.quote cite:before {
	content: '– ';
}
.quote h2 {
	font-size: 13px;
	font-weight: 600;
	text-transform: uppercase;
	color: #b31b1b;
	margin-bottom: 1.5em;
}
#home-header .secondary .contact-button {
	padding: 0 #{$grid-gutter-width/1.5};
	flex-grow: 1;
}
#home-header .secondary .contact-us {
	margin: 0 auto 1em;
}
#home-header .secondary .contact {
	margin: 0 #{-$grid-gutter-width/1.5};
}
#home-header .secondary .contact ul {
	margin: 0 auto 1.5em;
	width: auto;
	padding: 0 #{$grid-gutter-width/1.5};
	display: flex;
	align-items: center;
}
#home-header .secondary .contact li:last-child {
	margin-right: 0;
	padding-right: 0;
}
#home-header .secondary .contact li {
	border-color: rgba(0,0,0,0.12);
	display: flex;
	align-items: stretch;
}
#home-header .secondary .faculty-button {
	background: #d8e7ec;
	color: #2f3031;
	font-family: $project-font-family-sans-serif;
	font-size: 14px;
	position: relative;
	padding: 11px 14px 11px 40px;
	margin: 0 auto 1.5em;
}
#home-header .secondary .faculty-button:hover, #home-header .secondary .faculty-button:focus {
	color: #000;
}
#home-header .secondary .faculty-button:active {
	background: #cedee5;
}
#home-header .secondary .faculty-button .icon {
	position: absolute;
	left: 10px;
	top: 6px;
	font-size: 24px;
	color: #b31b1b;
}
#home-header .secondary .faculty-button .deco {
	text-decoration: underline;
	line-height: 14px;
	-moz-text-decoration-color: rgba(0,0,0,0.4);
	-webkit-text-decoration-color: rgba(0,0,0,0.4);
	text-decoration-color: rgba(0,0,0,0.4);
}
#home-header .secondary .faculty-button:hover .deco, #home-header .secondary .faculty-button:focus .deco {
	-moz-text-decoration-color: rgba(0,0,0,0.8);
	-webkit-text-decoration-color: rgba(0,0,0,0.8);
	text-decoration-color: rgba(0,0,0,0.8);
}
#home-header .secondary .faculty-button:hover .icon, #home-header .secondary .faculty-button:focus .icon {
	color: #2f3031;
}

/* Innovation Showcase */
.home main .row > .secondary:after {
	content: none;
}
.home .showcase {
	padding: 15px 0;
}
.showcase h2, #home-events h2 {
	padding-bottom: 0.25em;
	border-bottom: double rgba(0,0,0,0.12);
	font-size: 28px;
	max-width: 480px;
	margin: 0 auto;
}
#home-events h2 {
	margin-bottom: 1.5em;
}
.showcase h2 em {
	color: #b31b1b;
}
.showcase .card {
	margin-bottom: 50px !important;
}
.showcase .card img {
	border-bottom: 6px solid #b31b1b;
}
.showcase .category {
	text-transform: uppercase;
	color: #666;
	font-size: 16px;
	margin-bottom: 0.25em;
	text-decoration: none;
}
.showcase a {
	text-decoration: none;
	display: block;
	transition: background 0.15s, box-shadow 0.15s;
}
.showcase a:hover, .showcase a:focus {
	background: rgba(0,0,0,0.05);
	box-shadow: 0 0 0 15px rgba(0,0,0,0.05);
}
.showcase a:active {
	background: rgba(0,0,0,0.09);
	box-shadow: 0 0 0 15px rgba(0,0,0,0.09);
}
.showcase a h3 {
	text-decoration: underline;
	color: $project-color-link !important;
}
.showcase a:hover h3, .showcase a:focus h3 {
	color: #000 !important;
}
.showcase a:active h3 {
	color: $project-color-link-active !important;
}

/* Events */
#home-events {
	background: #f4f6f8;
	padding-top: 40px;
	padding-bottom: 40px;
	overflow: hidden;
}
#home-events .header-with-button {
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
}
#home-events .link-more .fa {
	margin: 0 -0.05em 0 0.5em;
	position: static;
}
#home-events .cwd-component, #elements {
	margin: 0 auto 2.5em;
	max-width: 480px;
	float: none;
}
.cwd-component .events .field {
	clear: both;
}
.cwd-component .events time {
	width: auto;
	min-height: 0;
	margin: 0 0 1em;
}
.cwd-component .events month, .cwd-component .events day {
	float: left;
	font-size: 15px;
	line-height: 1;
	font-weight: 400;
	padding: 9px 12px;
	background: #b83739;
}
.cwd-component .events day {
	background: #a63133;
}
.cwd-component .events h3 {
	width: 100%;
	font-size: 21px;
	line-height: 1.4;
	margin-bottom: 0.25em;
	font-family: $project-font-family-serif-alt;
}
.cwd-component .events .meta {
	color: $project-color-min-contrast !important;
	font-size: 13px;
	font-weight: 400;
}
.cwd-component .events a {
	display: block;
	text-decoration: none;
	color: #000;
	transition: background 0.15s, box-shadow 0.15s;
}
.cwd-component .events a:hover h3, .cwd-component .events a:focus h3 {
	text-decoration: underline;
	color: $project-color-link-events-hover !important;
}
.cwd-component .events a:active h3 {
	color: #000;
}
.cwd-component .events a:hover, .cwd-component .events a:focus {
	background: rgba(0,0,0,0.05);
	box-shadow: 0 0 0 15px rgba(0,0,0,0.05);
}
.cwd-component .events a:active {
	background: rgba(0,0,0,0.09);
	box-shadow: 0 0 0 15px rgba(0,0,0,0.09);
}

/* start psw */
a.view-all-events {
	display: inline-block;
	text-decoration: none;
	text-align: right;
	width: 100%;
	padding: 5px 0;
	text-transform: uppercase;
	font-weight: bold;
}
a.view-all-events:hover,
a.view-all-events:focus {
	text-decoration: underline;
	color: $project-color-link-hover-ow;
}
a.view-all-events:active {
	text-decoration: underline;
}
.padded-left {
	padding-left: 10px;
}
/* end psw */


/* Elements */
#elements {
	margin-top: -4px;
	margin-bottom: 20px;
}
#elements .element {
	position: relative;
	top: 0;
	opacity: 1;
	transition: top 0.5s, opacity 0.5s;
}
#elements .element a {
	position: relative;
	display: block;
	background: #a9c6cf;
	padding: 30px 25px 35px;
	margin: 0 0 -10px;
	border-radius: 20px 0 0 0;
	color: #000;
	text-decoration: none;
	border: 4px solid #f4f6f8;
	transition: background 0.15s;
	min-height: 200px;
}
#elements .element a:hover, #elements .element a:focus {
	background: #82b4c3;
}
#elements .element a:before {
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: -moz-radial-gradient(top left, ellipse cover, rgba(255,255,255,0.4) 0%, rgba(255,255,255,0) 100%);
	background: -webkit-radial-gradient(top left, ellipse cover, rgba(255,255,255,0.4) 0%,rgba(255,255,255,0) 100%);
	background: radial-gradient(ellipse at top left, rgba(255,255,255,0.4) 0%,rgba(255,255,255,0) 100%);
}
#elements .element:nth-child(2) a {
