		&:before {
			content: '\f150';
			font-size: 16px;
			color: #3787b0;
			position: absolute;
			left: 1px;
			top: 1px;
		}
	}
}
.page-node-type-basic-page .metadata-set, .page-node-type-resource .metadata-set {
	margin: -0.5em 0 3em;

	& + figure {
		margin-top: 0;
	}
}

/** resource library **/

.resource-library {

	background: rgba(241,241,241,.35);
	padding: 25px 25px 25px 15px;
	margin-left: -15px;
	clear: both;


	h2 {
		color: #b31b1b !important;
		font-size: 24px;
		font-family: $cwd-font-family-serif-alt;
		font-weight: 400;
	}

	ul {
		list-style: none;
		padding-left: 0px;
		font-family: $cwd-font-family-sans-alt-bold;
		font-weight: 400;

		li {

			font-size: 18px;
			padding: 8px 0px;

			.ext-resource {
				color: black;
			}

			a {
				color: #333;
				line-height: 1.4;
				font-weight: 400;
				text-decoration: none;

				&:hover {
					color: #b31b1b;
				}

				span.ext {
					background: transparent;
					text-decoration: none;

					&:after {
						font: normal normal normal 14px/1 FontAwesome;
						speak: none;
						font-size: 12px;
						text-rendering: auto;
						-webkit-font-smoothing: antialiased;
						-moz-osx-font-smoothing: grayscale;
						text-decoration: none !important;
						content: ' \f08e';
						color: inherit;
					}


	}
			}

		}
	}

	@media only screen and (max-width: $screen-sm-min) {
		display: none;
	}

	@media only screen and (min-width: $screen-sm-min) and (max-width: $screen-lg-min) {
		margin-left: -10px;
		padding-left: 10px;
	}
}

table {

		border: 1px solid #ddd;
		border-collapse: collapse;

	tr {

		border-bottom: 1px solid #ddd;

		th {
			border-bottom: 1px solid #ddd;
			text-align: left;
			padding: 10px;
			border-left: 1px solid #ddd;
			border-right: 1px solid #ddd;

		}

		td {
			padding: 10px;
			border-left: 1px solid #ddd;
			border-right: 1px solid #ddd;
			vertical-align: top;

			ul {
				padding-left: 20px;

				li {
					line-height: 1.4;
				}
			}

		}

		&:nth-child(even){
			background: rgba(241,241,241,.8);
		}
	}
}




ol,
ol ol ol ol,
ol ol ol ol ol ol ol {
    list-style-type:decimal;
}
ol ol,
ol ol ol ol ol,
ol ol ol ol ol ol ol ol {
    list-style-type:lower-alpha;
}
ol ol ol,
ol ol ol ol ol ol,
ol ol ol ol ol ol ol ol ol {
    list-style-type:lower-roman;
}


/* base.scss 1549 */
.breadcrumb li a .limiter {
	vertical-align: middle;
}
.breadcrumb li a:after {
	vertical-align: middle;
	bottom: -2px;
}



/* Removes png background from external links and adds in fontawesome glyphicon */
span.ext {
	background: transparent;
	text-decoration: none;

	&:after {
		font: normal normal normal 14px/1 FontAwesome;
		speak: none;
		font-size: .75rem;
		text-rendering: auto;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
		text-decoration: none !important;
		content: ' \f08e';
		color: inherit;
	}
}

.cwd-component .subheading h4 {
  color: #ab6161 !important;
}

.pager ul li.is-active a {
  background: #327caf !important;
}
